.am-wingblank.am-wingblank-lg,
.am-wingblank {
  margin: 0 !important;
}
.business_outer_wrap {
  background-color: #fff;
  padding: 10px 0;
  .business_outer_ul {
    display: flex;
    justify-content: space-around;
    padding-left: 0;
    .business_outer_li {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      img {
        width: 28px;
        height: 28px;
      }
      span {
        margin-top: 5px;
        font-size: 14px;
        color: #666;
      }
    }
    .img_wrap {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }
    .b1 {
      background: cornflowerblue;
    }
    .b2 {
      background: rgb(255, 120, 117);
    }
    .b3 {
      background: rgb(54, 207, 201);
    }
    .b4 {
      background: rgb(250, 140, 22);
    }
  }
}
.notice_bar_wrap {
  display: flex;
  align-items: center;
  background-color: #fff;
  padding: 5px 1vw;
  .am-notice-bar {
    background-color: #fff !important;
    color: #333 !important;
  }
  .notice_bar_title {
    min-width: 100px;
    font-weight: bold;
  }
}
.hot_games_wrap {
  .am-tabs-default-bar-underline {
    border: none !important;
  }
  .game_ul {
    width: 100%;
    padding: 20px 0;
    background-color: #fff;
  }
  .game_item {
    width: 25%;
    margin-bottom: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    float: left;
    background-color: #fff;
    img {
      width: 50px;
      height: 50px;
      border-radius: 5px;
    }
    .game_name {
      margin-top: 5px;
      font-size: 14px;
      color: #666;
    }
  }
}
.notice_carousel .v-item {
  height: 36px;
  line-height: 36px;
  padding-left: 10px;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.advertisement_wrap {
  padding: 0 20px 20px;
  background: #fff;
}
